import { 
	getArticle, 
	getTags, 
	getFeedArticle,
	addFavorite,
	deletFavorite
} from '@/api/api'
import { mapState } from 'vuex'
export default {
	name:"homePage",
	// 服务端异步渲染 数据会合并到data中
	async asyncData({ query, store}) {
		// console.log(store)
		const limit = 20
		const page = Number.parseInt(query.page || 1)
		const { tag } = query
		const tab = query.tab || 'global_feed'
		const loadArticle = store.state.user && tab === 'your_feed' 
		? getFeedArticle
		: getArticle
		// 优化异步并行任务
		const [articleRes, taglistRes] = await Promise.all([
			loadArticle({
				limit,
				offset: (page-1) * limit,
				tag
			}),
			getTags()
		])
		const { articles, articlesCount} = articleRes.data
		const { tags } = taglistRes.data
		articles.forEach(item=> item.favoritDiabled = false)
		return {
			articles,
			articlesCount,
			tags,
			limit,
			page,
			tag,
			tab, 
		}
	},
	// 监听page变化重新加载数据
	watchQuery: ['page', 'tag', 'tab'],
	computed: {
		...mapState(['user']),
		totlePage(){
			return Math.ceil(this.articlesCount/this.limit)
		}
	},
	methods: {
		async btnZ (article) {
			article.favoritDiabled = true
			if(article.favorited){
				// 取消点赞
				await deletFavorite(article.slug)
				article.favorited = false
				article.favoritesCount += -1
			}else{
				// 需要点赞
				await addFavorite(article.slug)
				article.favorited = true
				article.favoritesCount += 1
			}
			article.favoritDiabled = false
		}
	},
};
